footer {
  display: flex;
  height: 125px;
  gap: 55px;
}
@media (max-width: 767.98px) {
  footer {
    flex-direction: column;
    gap: 35px;
  }
}
footer .footer-article {
  display: flex;
  gap: 20px;
  width: 330px;
}
footer .footer-article img {
  height: 125px;
}
footer .footer-article .footer-article--text {
  display: flex;
  flex-direction: column;
  height: 125px;
}
footer .footer-article .footer-article--text .numb-footer-article {
  font-size: 30px;
  color: hsl(5, 85%, 63%);
  margin-bottom: 10px;
}
footer .footer-article .footer-article--text h3 {
  margin-bottom: 10px;
  font-size: 15px;
  color: black;
}
footer .footer-article .footer-article--text h3:hover {
  color: hsl(35, 77%, 62%);
}

header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 40px;
}
@media (max-width: 767.98px) {
  header {
    width: 340px;
  }
}
header .icon-menu {
  cursor: pointer;
  display: none;
}
@media (max-width: 767.98px) {
  header .icon-menu {
    display: flex;
    height: 20px;
  }
}
header .nav {
  padding: 10px 0px;
}
@media (max-width: 767.98px) {
  header .nav {
    display: none;
  }
}
header .nav ul {
  display: flex;
  gap: 40px;
}
header .nav ul .nav-li {
  text-decoration: none;
  list-style: none;
}
header .nav ul .nav-li a {
  color: hsl(236, 13%, 42%);
  text-decoration: none;
}
header .nav ul .nav-li a:hover {
  color: hsl(5, 85%, 63%);
}
header .nav-mobile-wrapper {
  display: none;
  position: fixed;
  min-width: 100%;
  min-height: 100%;
  left: 0px;
  top: 0px;
}
header .nav-mobile-wrapper .dark-fone {
  background-color: rgba(0, 0, 25.5, 0.575);
  width: 30%;
}
header .nav-mobile-wrapper .nav-mobile {
  display: grid;
  padding: 5%;
  width: 70%;
  background-color: hsl(36, 100%, 99%);
  grid-template-rows: 35px;
  gap: 90px;
}
header .nav-mobile-wrapper .nav-mobile img {
  align-self: end;
  justify-self: end;
}
header .nav-mobile-wrapper .nav-mobile ul {
  font-size: 20px;
  align-self: start;
  justify-self: start;
}
header .nav-mobile-wrapper .nav-mobile ul .nav-li {
  margin-bottom: 30px;
  text-decoration: none;
  list-style: none;
}
header .nav-mobile-wrapper .nav-mobile ul .nav-li a {
  color: hsl(240, 100%, 5%);
  text-decoration: none;
}
header .nav-mobile-wrapper .nav-mobile ul .nav-li a:hover {
  color: hsl(5, 85%, 63%);
}

main {
  display: flex;
  gap: 40px;
  margin-bottom: 70px;
  height: 510px;
}
@media (max-width: 767.98px) {
  main {
    display: flex;
    flex-direction: column;
    height: max-content;
  }
}
main aside {
  background-color: hsl(240, 100%, 5%);
  padding: 40px 25px;
  width: 350px;
}
@media (max-width: 767.98px) {
  main aside {
    width: 340px;
    padding: 25px 25px;
  }
}
main aside .news-section-title {
  color: hsl(35, 77%, 62%);
  font-size: 30px;
  margin-bottom: 35px;
}
main aside article {
  cursor: pointer;
}
main aside article a {
  text-decoration: none;
}
main aside article .article-title {
  margin: 10px 0px;
  color: hsl(36, 100%, 99%);
  font-size: 20px;
}
main aside article h2:hover {
  color: hsl(35, 77%, 62%);
}
main aside hr {
  margin: 30px 0px;
}
main .main-article {
  display: grid;
  grid-template-columns: 3fr 1.5fr 1.5fr;
  width: 730px;
}
@media (max-width: 767.98px) {
  main .main-article {
    width: 340px;
    gap: 20px;
    margin-bottom: 40px;
  }
}
main .main-article img {
  height: 300px;
  width: 730px;
  grid-column-start: 1;
  grid-column-end: 4;
  grid-row-start: 1;
  grid-row-end: 2;
  margin-bottom: 30px;
}
@media (max-width: 767.98px) {
  main .main-article img {
    width: 340px;
    margin-bottom: 20px;
  }
}
main .main-article .big-title {
  width: 365px;
  color: hsl(240, 100%, 5%);
  font-weight: 900;
  grid-row-start: 2;
  grid-row-end: 3;
  font-size: 55px;
  line-height: 1;
}
@media (max-width: 767.98px) {
  main .main-article .big-title {
    width: 340px;
  }
}
main .main-article .readMore-block {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 365px;
  grid-row-start: 2;
  grid-row-end: 3;
}
@media (max-width: 767.98px) {
  main .main-article .readMore-block {
    width: 340px;
    grid-row-start: 3;
    grid-row-end: 4;
  }
}
main .main-article .readMore-block .article-parag {
  width: 330px;
  margin-bottom: 25px;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Inter", sans-serif;
}
* img {
  display: inline-block;
}

body {
  display: grid;
  place-items: center;
}

.wrapper {
  margin: 2.5%;
  width: 1110px;
  height: 805px;
}
@media (max-width: 767.98px) {
  .wrapper {
    width: 340px;
    height: max-content;
    margin: 10% 2.5%;
  }
}

a {
  text-decoration: none;
}

.log {
  width: 40px;
  height: 65px;
}

.article-parag {
  color: hsl(236, 13%, 42%);
  font-size: 15px;
  line-height: 1.75;
}

.article-parag-1 {
  color: hsl(233, 8%, 79%);
  font-size: 14px;
  line-height: 1.75;
}

.readMore-btn {
  cursor: pointer;
  width: 185px;
  height: 50px;
  background-color: hsl(5, 85%, 63%);
  border: 0px;
  color: hsl(240, 100%, 5%);
  font-size: 13px;
  font-weight: bold;
  letter-spacing: 5px;
  padding: 15px 30px;
}

.readMore-btn:hover {
  color: hsl(36, 100%, 99%);
  background-color: hsl(240, 100%, 5%);
}

hr {
  color: hsl(236, 13%, 42%);
  border: 1px solid hsl(236, 13%, 42%);
}

.nav-mobile-wrapper.active {
  display: flex;
}